.header-search-bar {
  height: 36px;
  border: 1px solid #979797;
  border-radius: 4px;
  background-color: white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
  overflow: hidden;
}

.header-search-bar-input {
  height: 100%;
  -ms-flex-positive: 1;
      flex-grow: 1;
  border: none;
  height: 34px;
  text-indent: 8px;
  font-size: 16px;
  width: 350px;
}

.header-search-bar-input:focus {
  outline: 0;
}

.header-search-bar-button {
  margin-right: 4px;
  text-transform: uppercase;
  border-radius: 4px;
  background-color: #550000;
  color: #ffffff;
  text-align: center;
  line-height: 26px;
  height: 26px;
  font-size: 14px;
  padding: 0 8px;
  border: none;
}



