.login-container {
  width: 330px;
  margin: auto;
  margin-top: 96px;
  border-radius: 3px;
  background-color: #fbfbfa;
  border-radius: 6px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  /*box-shadow: 0px 2px 4px #999;*/
  border: 1px solid #e9e9e9;
  padding: 8px 8px 1em 8px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.login-head-text {
  margin-top: 24px;
  font-size: 18px;
  color: #171e27;
  margin-bottom: 24px;
}

.login-form-element-label {
  font-size: 20px;
  color: #171e27;
  margin-bottom: 4px;
  margin-top: 8px;
}

.login-form-element-input {
  -webkit-box-shadow: inset 0px 1px 3px #999;
          box-shadow: inset 0px 1px 3px #999;
  border-radius: 4px;
  width: 300px;
  height: 48px;
  border: 1px solid #979797;
  font-size: 20px;
  padding: 0 8px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.login-form-button {
  color: #fff;
  border-radius: 4px;
  width: 300px;
  border: none;
  background-color: #530000;
  font-size: 20px;
  padding: 0.7em 8px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin-top: 16px;
}

.login-form-link-text {
  margin-top: 24px;
}

.login-form-error {
  color: #e60000;
  font-weight: bold;
}



